import React from 'react'
import { Alert, AlertIcon, AlertDescription, CloseButton, useDisclosure } from '@chakra-ui/react'

export default function Error() {
    return (
        <Alert margin='15px auto' pos='relative' top='0' borderRadius='5px' padding='5px 5px 5px 10px' status='error' height='40px' width='300px' bg='red'>
            <AlertIcon height='25px' width='25px' />
            <AlertDescription ml='10px'> () /</AlertDescription>
        </Alert>
    )
}
